.center {
  text-align: center;
}

h5 {
  margin-top: 5px;
  margin-bottom: 5px;
  font-size: 18px;
}

.filter-by {
  cursor: pointer;
  padding: 2px;
  height: 30px;
  outline-color: purple;
  margin-left: 5px;
  margin-top: 5px;
  margin-bottom: 5px;
  font-size: 15px;
}

.clear-filters {
  display: block;
  margin-left: 50%;
  transform: translateX(-50%);
  margin-top: 10px;
  height: 35px;
  padding: 0 10px;
  background-color: rgba(26, 24, 24, 0.951);
  color: white;
  border: none;
  cursor: pointer;
  font-size: 13px;
  font-weight: 500;
}

.clear-filters:active {
  transform: translate(-50%, 1px);
}
